/* General Media Query ------------------------------------- */
@media (max-width: 767px) {
  nav.navbar .search-btn {
    margin-left: 0;
    padding-left: 0;
    border-left: none;
  }
}

@media (max-width: 575px) {
  nav.navbar .container {
    width: 100%;
  }
  .breadcrumb li {
    display: block;
    width: 100%;
    text-align: center;
